/* Feedback for Project images */
a > img:hover {
  padding: 0 5px 5px 0;
}

.color img {
  filter: grayscale(100%);
  -webkit-filter: grayscale(100%);
  -webkit-transition: all 0.3s ease;
  border-radius: 5px;
  border: none;
  width: 100%;
  height: auto;
}

.color img:hover {
  filter: grayscale(0%);
  filter: gray;
  -webkit-filter: grayscale(0%);
  filter: none;
  transition: 0.3s ease;
  box-shadow: 0 3px 20px #66fcf1;
}

/* Feedback for Portfolio buttons */
.appLinks {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.appLinks > a {
  border-radius: 5px;
  color: white;
  margin: 10px 10px 0 0;
  border: 1px solid #ffffff;
}

.appLinks > a:hover {
  background-color: #66fcf1;
  border-radius: 5px;
  margin: 10px 10px 0 0;
  color: black;
  box-shadow: 0 3px 5px #ffffff;
}
